* {
    margin: 0;
    padding: 0;
    font-family: var(--font) !important;
    color: var(--color);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
body {
    background-color: var(--bgBody) !important;
}
header {
    box-shadow: none !important;
    border-bottom: 1px solid lightgrey !important;
    background-color: var(--bgHeader) !important;
}
header * {
    color: var(--color) !important;
}
main {
    padding: 5rem 0px !important;
}
button {
    display: flex !important;
    align-items: center !important;
}

.add-button {
    color: var(--helpColor) !important;
}
.logo {
    display: flex;
    align-items: center;
    gap: 0.5rem !important;
    cursor: pointer;
}
.big-title {
    font-size: 3rem;
}
.note {
    color: var(--helpColor) !important;
}
.mt-5 {
    margin-top: 3rem;
}
.MuiList-root {
    background-color: var(--bgHeader);
    border-radius: 10px;
}
.MuiAccordion-root {
    width: 100%;
    background-color: var(--bgBody) !important;
    border-radius: 10px !important;
}
.MuiMenu-paper {
    background-color: var(--bgHeader) !important;
}
.block-addBot {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
    width: 100%;
}
@media (max-width: 900px) {
    .block-addBot {
        height: auto;
        flex-flow: column nowrap;
    }
    .block-addBot .MuiCard-root {
        max-width: none;
        width: 100% !important;
    }
    .block-addBot button {
        width: 100% !important;
    }
    .big-title {
        font-size: 3rem !important;
    }
    .maifs {
        flex-flow: column nowrap !important;
        gap: 5px !important;
    }
}
.MuiCard-root {
    background-color: var(--bgHeader) !important;
}
.MuiTypography-gutterBottom {
    min-height: 64px !important;
}
.wfull {
    width: 100% !important;
}
.MuiAlert-root * {
    color: rgb(95, 33, 32) !important;
}
.MuiAlert-root path {
    color: #ef5350 !important;
}
.bigL-title {
    font-size: 3rem !important;
    font-weight: 900;
}
.header-buttons {
    gap: 0.75rem !important;
    margin-left: 1rem;
}
.second-startPage-block {
    width: 80%;
    font-size: 1.5rem;
    margin-top: 3rem;
}
.second-startPage-block p {
    font-weight: 350;
    margin-bottom: 10px;
}
.addButton-startPage {
    background-color: var(--helpColor) !important;
    font-size: 1.1rem !important;
    margin-top: 1.5rem !important;
}
.infoButton-startPage {
    background-color: transparent !important;
    color: var(--color) !important;
    font-size: 1.1rem !important;
    margin-top: 0.3rem !important;
}
.MuiList-root * {
    color: var(--color) !important;
}
.aboutPage-block {
    width: 80%;
}
.aboutPage-block .info-block-text {
    display: flex;
    flex-flow: row nowrap;
    gap: 1rem;
    width: 100%;
}
.aboutPage-block .info-block-text:not(:last-child) {
    margin-bottom: 1rem;
}
.hnfull {
    min-height: unset !important;
    height: auto !important;
}
.button-disabled {
    background-color: var(--helpColor) !important;
}
.card-about * .nopad {
    padding: 0px !important;
}