.itemShop {
    width: 365px;
    cursor: pointer;
    transition: opacity 0.3s ease 0s !important;
    transition: all 0.3s ease 0s !important;
}
.itemShop  * {
    color: var(--color) !important;
}
.itemShop path {
    color: var(--colorWhite) !important;
}
.itemShop:hover {
    opacity: 0.9 !important;
    transform: translateY(-3px) !important;
}
.block-addBot {
    flex-wrap: wrap !important;
    gap: 1rem;
}
.block-addBot > div > img {
    background-size: -3rem !important;
}
.itemShop-up {
    height: 100px !important;
}
.itemShop-uplow {
    height: 87px !important;
}

.itemShop-price {
    display: flex !important;
    flex-flow: row nowrap !important;
    justify-content: space-between;
    align-items: center !important;
    padding: 16px !important;
    padding-bottom: 0px !important;
    margin-top: 10px;
}
.itemShop-price .itemShop-button {
    width: max-content !important;
    background-color: var(--addColor) !important;
    color: var(--colorWhite) !important;
    transition: all 0.3s ease;
}
.itemShop-price .itemShop-button[disabled] {
    background-color: var(--helpColor) !important;
}
.itemShop-price .itemShop-button:hover {
    padding-left: 2rem !important;
    padding-right: 2rem !important;
    transform: translateX(-10px) !important;
}
.price-block {
    display: flex !important;
    flex-flow: column nowrap !important;
    gap: 1px !important;
}
.price-block .MuiTypography-h6 p {
    position: relative;
    top: -10px !important;
    font-weight: 600 !important;
    color: var(--helpColor) !important;
    text-decoration: line-through 1px !important;
}
.MuiBadge-badge {
    background-color: var(--saleColor) !important;
    color: var(--colorWhite) !important;
    top: -7px !important;
    left: 21px !important;
}
.nosell {
    margin-top: 0px !important;
}
.nomt10 {
    margin-top: -22px !important;
}

.add-button {
    color: var(--color) !important;
}
.fa-b {
    display: flex;
    flex-flow: column nowrap;
}
.header-right {
    display: flex;
    flex-flow: row nowrap;
}
.like-button path {
    color: var(--color) !important;
}
.flex-row {
    display: flex;
    flex-flow: row nowrap;
    align-items: center !important;
    gap: 0.5rem !important;
}
.liked path {
    color: var(--saleColor) !important;
}
.header-search {
    width: 300px !important;
    -webkit-flex-direction: unset !important;
    flex-direction: unset !important;
    padding: 0.5rem 0 !important;
}
.header-search input {
    width: 300px !important;
}
.css-1ptx2yq-MuiInputBase-root-MuiInput-root:after {
    border-color: var(--addColor) !important;
}
.bpbab div {
    width: 49% !important; 
}
.bpbab div div {
 width: 95% !important;
}
.bpbab div div button {
    background-color: var(--addColor) !important;
    color: var(--colorWhite) !important;
}
.bpbab div div button path {
    color: var(--colorWhite) !important;
}
.gap-1 {
    display: flex;
    flex-flow: column nowrap;
    gap: 1rem;
}
.gap-1 div p {
    font-size: 1.5em !important;
}
.jsb {
    justify-content: space-between;
}
.MuiDialog-paper {
    min-width: 30%;
}
.MuiDialog-paper p {
    font-weight: 600 !important;
}
.card-purpleorange {
    width: 49%;
    height: 300px;
    background-color: #FA8BFF;
    background-image: linear-gradient(45deg, #FA8BFF 0%, #2BD2FF 52%, #2BFF88 90%);
}
.card-blue {
    width: 49.8%;
    height: 300px;
    background-color: #85FFBD;
    background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
}
.card-full {
    width: 100% !important;
    height: 300px;
    background-color: #D9AFD9;
    background-image: linear-gradient(0deg, #D9AFD9 0%, #97D9E1 100%);
}
.card-about * {
    color: var(--color) !important;
    padding: 3rem !important;
}
.gap-2 {
    gap: 1rem !important;
}
.bpbab div div div div.wff {
    width: 100% !important;
}
.wff p {
    color: #444 !important;
}